.trips {
  .trips-title {
    margin-bottom: 30px;
  }

  .trips-grid-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 50px;

    li {
      display: flex;
      width: 500px;
      list-style: none;
      img {
        width: 200px;
        height: 200px;
        border-radius: 15px;
      }

      .trip-text-container {
        margin-inline-start: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .upper-section{
          p {
            color: $clr-grey;
            margin-bottom: 5px;
          }
        }
        .bottom-section {
          display: flex;
          align-items: center;
          gap: 5px;
        }
      }
    }
  }

  ul {
    margin-block-end: 30px;
  }

  h3 {
    margin-block-end: 30px;
  }

  h4 {
    margin-block-end: 5px;
  }
}