@media (max-width: 850px) {
    .app-header {
        .main-header{
            justify-content: space-between;
            padding-block-start: 1rem;

            .logo {
                margin-block-start: 0.5rem;
                margin-inline-end: 1.5rem;
                width: 30px;
                :first-child {
                    display: none;
                }
                :last-child {
                    display: inline;
                    width: 30px;
                }
            }
            .search-container{
                margin-right: auto;
                .right{
                    font-size: 0.8rem;
                }
            }
            .header-user{
                margin-block-start: 0.2rem;
            }
        }
        .expended-search{
            .bar{
                width: 650px;
            }
        }
        .locations-modal{
            left: 12%;
            width: 400px;
            .locations-modal-map{
                div p{
                    font-size: 0.8rem;
                }
                img{
                    height: 100px;
                }
            }
        }
    }
}